@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    html {
        line-height: 1.5;
        font-weight: 400;
        font-family: 'Azeret Mono', 'Noto Sans TC', -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", "Helvetica Neue", Arial, system-ui, monospace, sans-serif; 
    }
}

@layer utilities {
    /* 漸層 */
    .border-b-gradient {
        @apply border-b-4 border-transparent;
        border-image: linear-gradient(to right, #21D4FD, #B721FF) 1;
    }

    .border-gradient {
        @apply border rounded-full;
        background-image: linear-gradient(to bottom, #21D4FD, #B721FF);
    }

    .text-gradient {
        @apply bg-clip-text text-transparent;
        background-image: linear-gradient(to bottom, #21D4FD, #B721FF); 
    }

    /* 漸層動畫 */
    .animate-gradient {
        background-image: linear-gradient(90deg, #21D4FD, #55D7FF, #9F3BFF, #B721FF, #21D4FD);
        background-size: 300% 300%;
    }

    .hover-300 {
        @apply transition-all duration-300;
    }
}
@layer utilities {
    /* 輪播樣式 */
    .splide__pagination__page {
        @apply bg-primary h-[8px] w-[8px] m-[5px];
    }

    .splide__pagination__page.is-active {   
        @apply bg-primary rounded-[9999px] w-[16px];
    }

    .splide__arrow {
        @apply bottom-0 top-[90%];
    }

    .splide__pagination {
        @apply -bottom-8;
    }
}